
.app-wrapper {
    height:100vh;
    display: grid;
    grid-template-areas:
            "h h"
            "n c"
            "f f";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;

}

.app-wrapper-content {
    height: 100%;
    grid-area: c;
    background-color: #010409;
}